<template>
  <div
    class="sidebar"
    :class="{
      collapsed: isCollapsed,
      'sidebar-left': side === 'left',
      'sidebar-right': side === 'right',
    }"
  >
    <div class="main" :style="isCollapsed ? 'display:none' : 'display:block'">
      <slot></slot>
    </div>

    <span @click="toggleCollapse" class="toggle-icon">
      <span
        v-show="
          (side == 'left' && isCollapsed) || (side == 'right' && !isCollapsed)
        "
        >展开</span
      >
      <span
        v-show="
          (side == 'left' && !isCollapsed) || (side == 'right' && isCollapsed)
        "
        >收起</span
      >
    </span>
  </div>
</template>

<script>
export default {
  props: {
    side: {
      type: String,
      default: "left",
      validator: (value) => ["left", "right"].includes(value),
    },
  },
  data() {
    return {
      isCollapsed: true,
    };
  },
  methods: {
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed;
      this.$emit("toggleCollapse", this.isCollapsed, this.side);
    },
  },
};
</script>

<style lang="scss" scoped>
.sidebar {
  pointer-events: auto;
  width: 280px;
  margin: 17px 11px;
  height: calc(100% - 80px);
  padding-bottom: 10px;
  background-color: rgba(0, 13, 27, 0.9);
  border-radius: 4px;
  border: 1px solid;
  border-image: linear-gradient(
      90deg,
      rgba(7, 136, 255, 1),
      rgba(7, 136, 255, 0.5)
    )
    1 1;
  transition: width 0.3s ease;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  &.collapsed {
    width: 0;
    border: none;
  }

  .toggle-icon {
    position: absolute;
    top: 50%;
    width: 24px;
    font-size: 24px;
    cursor: pointer;
    transform: translateY(-50%);
    background-color: pink;
  }
}
.sidebar-left {
  left: 0;
  .toggle-icon {
    right: -32px;
  }
}

.sidebar-right {
  right: 0;
  .toggle-icon {
    left: -32px;
  }
}

.main {
  transition: display 0.3s ease;
  height: 100%;
  // overflow-y: scroll;
}
</style>



